<template>
  <div class="top">
    <div class="info_wrapper">
      <div class="name">
        <span>姓名</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入姓名" v-model="name"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>联系电话</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入代理绑定的手机号码" v-model="phone"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>微信号</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入微信号" v-model="weixin"/>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>身份证号</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请输入身份证号" v-model="user_id"/>
    </div>
  
    <div class="info_wrapper" @click="show=true">
      <div class="name">
        <span>申请区域</span>
        <span class="red">*</span>
      </div>
      <div class="weizhi" @click="show=true">
        <span>{{area}}</span>
      </div>
    </div>

    <div class="info_wrapper">
      <div class="name">
        <span>详细地址</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请填写详细地址" v-model="address_xx"/>
      <!-- <div class="weizhi" @click="address=true">
        <van-icon name="location" color="#15BDDB"/>
        <span style="color:#15BDDB;">{{address_search}}</span>
      </div> -->
    </div>

    <div class="info_wrapper" @click="yiji=true">
      <div class="name">
        <span>代理分成比</span>
        <span class="red">*</span>
      </div>
      <div class="weizhi">
        <span>{{yiji_value}}</span>
      </div>
      <!-- <input type="text" placeholder="请选择代理分成比" v-model="yiji_value"/> -->
    </div>

    <!-- <div class="info_wrapper" @click="panduan()">
      <div class="name">
        <span>二级分成比</span>
        <span class="red">*</span>
      </div>
      <input type="text" placeholder="请选择二级分成比" v-model="erji_value"/>
    </div> -->

    <div class="quanxian">
      <div>权限：</div>
      <van-checkbox-group v-model="result" class="mlr">
        <van-checkbox name="1" shape="square" checked-color="#15BDDB">修改酒店</van-checkbox>
        <van-checkbox name="2" shape="square" checked-color="#15BDDB">删除设备</van-checkbox>
        <van-checkbox name="3" shape="square" checked-color="#15BDDB">添加酒店</van-checkbox>
        <van-checkbox name="4" shape="square" checked-color="#15BDDB">店铺转移</van-checkbox>
      </van-checkbox-group>

      <!-- <van-checkbox-group v-model="result1" >

      </van-checkbox-group> -->
    </div>
    <!-- 提交 -->

    <div class="submit">
      <button class="btn" @click="getsetin()">提交申请</button>
    </div>

    <!-- 入驻身份单选 -->
    <van-Popup  v-model="activeNames" id="shenfen">
      <strong class="title">请选择入驻类型</strong>
      <van-radio-group v-model="radio" @change="getshenfen()">
        <van-radio :name="index" v-for="(item,index) in shenfen_data" :key="index">{{item}}</van-radio>
      </van-radio-group>
    </van-Popup >

    <!-- 选择地址 -->
    <van-popup v-model="show" position="bottom">
      <van-area :area-list="site" @cancel="show=false" @confirm="changeAddress"/>
    </van-popup>

    <!-- 选择地址 -->
    <van-popup v-model="address" position="bottom" :style="{ height: '100%',width: '100%' }">
      <div class="search">
        <input type="text" placeholder="请输入搜索关键字" v-model="address_key" @blur="getaddress()" class="input">
        <div @click="getaddress()" class="se">搜索</div>
      </div>
      <div class="item" @click="address=false;getjwd(item,index)"
      v-for="(item,index) in address_data" 
      :key="index">
      <van-icon name="location" color="#15BDDB"/>{{item.district}}{{item.address}}{{item.name}}</div>
    </van-popup>

    <!-- 一级分成比 -->
    <van-popup v-model="yiji" :style="{ padding:'50px',width:'60%'}" class="yiji_">
      <div class="yiji">
        <h4 style="text-align: center;margin-bottom:10px">请选择代理分成比</h4>
        <van-radio-group v-model="radio" @change='getyiji'>
          <van-radio name="1">10%</van-radio>
          <van-radio name="2">20%</van-radio>
          <van-radio name="3">30%</van-radio>
          <van-radio name="4">40%</van-radio>
          <van-radio name="5">50%</van-radio>
          <van-radio name="6">60%</van-radio>
          <van-radio name="7">70%</van-radio>
          <van-radio name="8">80%</van-radio>
        </van-radio-group>
      </div>
    </van-popup>
    <van-popup v-model="erji" :style="{ padding:'50px',width:'60%'}" class="yiji_">
      <div class="erji">
        <h4 style="text-align: center;margin-bottom:10px">请选择二级分成比</h4>
        <van-radio-group v-model="radio1" @change='geterji'>
          <van-radio :name="item" v-for="(item,index) in erji_list" :key="index">{{item}}0%</van-radio>

        </van-radio-group>
      </div>
    </van-popup>

  </div>
</template>

<script>
import { getadd_proxy} from '@/axios/getData'
import { Popup, Radio, Area, RadioGroup, Icon, Toast, CheckboxGroup, Checkbox } from 'vant';
import util from '../utils/utils.js'  
export default {
  data(){
    return{
      // 权限
      result:[],
      result1:[],
      erji_list:99,
      yiji:false,
      yiji_value:'请选择代理分成比',
      radio:'',   //  身份选择  +3    对应3 4 5
      erji:false,
      erji_value:'',
      radio1:'',

      address_xx:'',   // 详细地址

      address_key:'',
      address_search:'请选择详细地址',  
      address:false,      //  定位
      address_data:[],    //  地理位置数据
      jd:'',              //  经纬度
      wd:'',
      mapshow:false,
      name:'',            //  用户名
      phone:'',           //  手机电话号码
      shenfen:'',         //  入驻身份 
      weixin:'',          //  微信号
      user_id:'',         //  身份证
      area:'请选择区域',   //  区域
      show:false,            //  选择地址
      // province:'',        //  省
      // city:'',            //  市
      // district:'',        //  区
      activeNames:false,
      shenfen_data:['酒店', '分销商', '总代理'],   
      data:[],
      isshow:false,       // 
      value1: 0,
      value2: 'a',
      option1: [
        { text: '全部商品', value: 0 },
        { text: '新款商品', value: 1 },
        { text: '活动商品', value: 2 }
      ],
      taocan:'',
      site: util.site,
    }
  },
  components: {
    vanPopup: Popup,
    vanRadio: Radio,
    vanArea: Area,
    vanRadioGroup: RadioGroup,
    vanIcon: Icon,
    vanCheckboxGroup: CheckboxGroup,
    vanCheckbox: Checkbox
  },
  created() {
    // this.getdata()
  },
  methods:{
    // 获取一级分成比
    getyiji(a){
      this.yiji_value = a*10+'%'
      this.erji_list = 8-a
      this.yiji = false
    },
    // 获取二级分成比
    geterji(a){
      console.log(a)
      if(this.yiji_value){
        this.erji_value = a*10+'%'
        this.erji = false
      } else{
        this.erji = false
        Toast('请您先设置一级分成比!')
      }
    },
    // 先设置一级后设置二级
    panduan(){
      if(this.yiji_value&&!this.erji_list==0){
        this.erji = true
        return
      }else if(this.erji_list==99){
        this.erji = false
        Toast('请您先设置一级分成比!')
        return
      }
      if(this.erji_list==0){
        this.erji = false
        Toast('总分成不能高于80%')
        return;
      } 
    },
    // 数据提交
    getsetin(){
      if( this.name&& this.phone&& this.weixin&& this.user_id&& this.area&&this.yiji_value){
        var data = {}
        data.one_level = this.yiji_value.substr(0,2)
        // data.two_level = this.erji_value.substr(0,2)
        data.username = this.name;
        data.mobile = this.phone;
        data.wx_number = this.weixin;
        data.code_id = this.user_id;
        data.region = this.area;
        data.address = this.address_xx
        data.jurisdiction = this.result.join(',')
        // data.type = this.radio+3;
        // data.jd = this.jd;
        // data.wd = this.wd;
        // console.log(data);
        // return;
        getadd_proxy(data).then((res)=>{
          if(res.code==200){
            Toast(res.data);
            this.$router.go(-1)
          } else if(res.code==10000){
            Toast(res.msg);
            // this.$router.go(-1)
          }
          // console.log(res.msg)
        })
      } else {
        Toast('请完善入驻信息')
      }
    },
    // 点击获取经纬度
    getjwd(item,i){
      this.jd = item.location.split(',')[0];
      this.wd = item.location.split(',')[1];
      this.address_search = item.district +'-'+item.address +'-'+item.name
    },
    // 获取详细地址  == 经纬度
    getaddress(){
      var  that = this
      if(!that.address_key){
        Toast('请输入搜索关键字');
        return;
      }
      axios.get("https://restapi.amap.com/v3/assistant/inputtips", {
        params: {
          key: "182a36b8ef5943532fa7d717f378e858",
          keywords: that.address_key
        }
      }).then(res => {
        // console.log(res)
        that.address_data = res.tips;
      });
    },
    // 获取身份
    getshenfen(){
      this.activeNames = false;
      this.shenfen = this.shenfen_data[this.radio]
    },
    // 变更地址
    changeAddress(val) {
      this.area = val[0].name +'-'+ val[1].name +'-'+ val[2].name
      this.show = false
    },
    saoma(){
      wx.ready(() => {
        wx.getLocation({
          type: 'wgs84', // 默认为wgs84的gps坐标，如果要返回直接给openLocation用的火星坐标，可传入'gcj02'
          success: function (res) {
            var latitude = res.latitude; // 纬度，浮点数，范围为90 ~ -90
            var longitude = res.longitude; // 经度，浮点数，范围为180 ~ -180。
            var speed = res.speed; // 速度，以米/每秒计
            var accuracy = res.accuracy; // 位置精度
            console.log(res)
          }
        });
      });
    },
    getdata(){
      var that = this
      var url = window.location.href
      getmap({url}).then(res => {
        console.log(res.data)
        let data = res.data;
        let op = {
          debug: false,
          appId: data.appId, //公众号的唯一标识
          timestamp: data.timestamp, // 生成签名的时间戳
          nonceStr: data.nonceStr, // 生成签名的随机串
          signature: data.signature, // 签名
          jsApiList: ["getLocation"] // 需要使用的JS接口列表(我只需要调用扫一扫的接口，如有多个接口用逗号分隔)
        };
        wx.config(op);
        that.saoma();
        wx.error(r => {

        });
      });
    },
  },
  mounted(){

  }
}
</script>

<style scoped lang="scss">
.top{
  padding:50px 15%;
  height:100vh;
  background: #FAFAFA;
}
  .info_wrapper {
    padding: 25px 0;
    box-sizing:border-box;
    border-bottom: 1px solid rgb(238, 238, 238);
    display: flex;
    align-items: center;
    .name{
      width:27%;
      text-align: right;
    }
    .red {
      color: #e62121;
    }
    .weizhi{
      border: 1px solid #DEDEDE;
      margin-left:20px;
      padding:10px;
      box-shadow: 0 0 7px #DEDEDE inset;
      flex:1;
      background: #fff;
    }
    input {
      border: 1px solid #DEDEDE;
      margin-left:20px;
      padding:10px;
      box-shadow: 0 0 7px #DEDEDE inset;
      flex:1;
    }
  }
  input::-webkit-input-placeholder{
    color:#999;
  }
  #shenfen{
    width:50%;
    padding:50px;
    border-radius: 10px;
    .title{
      text-align: center;
      display: inline-block;
      width:100%;
      margin-bottom:20px;
    }
    /deep/ .van-radio{
      margin-bottom:15px;
    }
  }
  #ditu{
    width:101%;
    height:100%;
  }
  .search{
    display: flex;
    padding:5%;
    align-items: center;
    .input{
      width:70%;
      border:1px solid #eee;
      padding:10px;
    }
    .se{
      margin-left:30px;
      padding:10px 30px;
      background: #15BDDB;
      color:#fff;
    }
  }
  .item{
    padding:25px 30px;
    border-bottom:1px solid #eee;
    display: flex;
    align-items: center;
    /deep/ .van-icon{
      margin-right:15px;
    } 
  }
  .submit{
    position: fixed;
    bottom:0;
    // z-index: 1;
    // text-align: center;
    padding:15px 0;
    background: #fff;
    width:100%;
    // border-top:1px solid #eee;
    .btn{
      font-size: 30px;
      width:70%;
      background: #15BDDB;
      color:#fff;
      border-radius: 50px;
      padding:20px 0;
    }
  }
  /deep/ .van-radio{
    margin-bottom:10px;
  }
  /deep/ .van-checkbox{
    margin-bottom:10px;
  }
  .yiji_{
    border-radius: 10px;
  }
  .quanxian{
    margin-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center
  }
  .mlr{
    margin:0 30px; 
  }
</style>



